$mobile-bg: white;
$mobile-bg-border: 8px solid #eb529d;

body.menu-open {
    overflow: hidden;
}
body > form {
    position: relative;
    overflow: hidden;
}
.navbar-toggle {
    display: block;
    position: absolute;
    top: 5px;
    right: 0;
    @media (max-width: 400px) {
        top: 1px;
    }
    .icon-bar {
        display: block;
        width: 27px;
        height: 2px;
        border-radius: 1px;
        background-color: $c-lightblue;
        &-text {
            font-size: 12px;
        }
    }
    @media (min-width: $grid-float-breakpoint) { display: none;  }
    .sr-only {
        position: relative;
        background-size: 27px 20px;
        margin-top: 2px;
        display: block;
        width: 26px;
        height: 19px;
        text-indent: -5000px;
        margin-right: -16px;
    }
}

@media (max-width: $grid-float-breakpoint-max) {
    .mobile-nav-container { display: block; }
    .navbar-top-level, #navbar-content { display: none; }
}

@media (min-width: $grid-float-breakpoint) {
    .mobile-nav-container { display: none; }
    .navbar-top-level, #navbar-content { display: block; }
}

.menu-trigger {
	position: relative;
	padding-left: 60px;
	font-size: 0.9em;
}

.menu-trigger:before {
	position: absolute;
	top: 2px;
	left: 0;
	width: 40px;
	height: 6px;
	background: #fff;
	box-shadow: 0 6px #34495e, 0 12px #fff, 0 18px #34495e, 0 24px #fff;
	content: '';
}

.mp-pusher {
	position: relative;
	left: 0;
	height: 100%;
    /*perspective: 1000px;*/ /* Disabled to fix the LPD sticky nav */
    /* Disable transition if greater than width so that a larger device with menu open snaps close instantly */
    @media (min-width: $grid-float-breakpoint) {
        transform: none !important;
    }

    /* Fix for the footer overlapping in the backend editor, we let the height flow by its content */
    body.sfPageEditor & {
        height: auto;
    }
}

.mp-menu {
	position: absolute; /* we can't use fixed here :( */
	top: 0;
	left: 0;
	z-index: 1;
	width: 300px;
	height: 100%;
	transform: translate3d(-100%, 0, 0);
    @media (max-width: 340px) {
        width: 270px;
    }
}

.mp-level {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $mobile-bg;
	transform: translate3d(-100%, 0, 0);
}

.menu-open .mp-pusher:before {
    @media (max-width: $screen-sm-max) {
        background: url(/custom/build/images/mobile-close-menu.png) no-repeat 50% 50%;
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 100000;
        color: #fff;
        padding: 20px 15px;
        width: 25px;
        height: 25px;
        background-size: 50%;
        cursor: pointer;
    }
}

/* overlays for pusher and for level that gets covered */
.mp-pusher::after,
.mp-level::after,
.mp-level::before {
	position: absolute;
	top: 0;
	right: 0;
	width: 0;
	height: 0;
	content: '';
	opacity: 0;
}

.mp-pusher::after,
.mp-level::after {
	background: rgba(0,0,0,0.8);
	transition: opacity 0.3s, width 0.1s 0.3s, height 0.1s 0.3s;
    z-index: 1000;
}

.mp-level::after {
	z-index: -1;
}

.mp-pusher.mp-pushed::after,
.mp-level.mp-level-overlay::after {
	width: 100%;
	height: 100%;
	opacity: 1;
	transition: opacity 0.3s;
    /* Disable transition if greater than width so that a larger device with menu open snaps close instantly */
    @media (min-width: $grid-float-breakpoint) {
        display: none;
    }
}

.mp-level.mp-level-overlay {
	cursor: pointer;
}

.mp-level.mp-level-overlay.mp-level::before {
	width: 100%;
	height: 100%;
	background: transparent;
	opacity: 1;
}

.mp-pusher,
.mp-level {
	/*transition: all 0.5s;*/
    transition: transform .5s;

    /* Disable transition if greater than width so that a larger device with menu open snaps close instantly */
    @media (min-width: $grid-float-breakpoint) {
        transition: none;
    }
}

/* overlap */
.mp-overlap .mp-level.mp-level-open {
	box-shadow: 1px 0 2px rgba(0,0,0,0.2);
	transform: translate3d(-40px, 0, 0);
}

/* First level */
.mp-menu > .mp-level,
.mp-menu > .mp-level.mp-level-open,
.mp-menu.mp-overlap > .mp-level,
.mp-menu.mp-overlap > .mp-level.mp-level-open {
	box-shadow: none;
	transform: translate3d(0, 0, 0);
}

/* cover */
.mp-cover .mp-level.mp-level-open {
	transform: translate3d(0, 0, 0);
}

.mp-menu .mp-level .mp-level {
    margin-top: 48px;
}

.mp-cover .mp-level.mp-level-open > ul > li > .mp-level:not(.mp-level-open) {
	transform: translate3d(-100%, 0, 0);
}

/* content style */
.mp-menu ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.mp-menu h2 {
	margin: 0;
	padding: 1em;
	color: rgba(0,0,0,0.4);
	text-shadow: 0 0 1px rgba(0,0,0,0.1);
	font-weight: 300;
	font-size: 2em;
}

.mp-menu.mp-overlap h2::before {
	position: absolute;
	top: 0;
	right: 0;
	margin-right: 8px;
	font-size: 75%;
	line-height: 1.8;
	opacity: 0;
	transition: opacity 0.3s, transform 0.1s 0.3s;
	transform: translateX(-100%);
}

.mp-menu.mp-cover h2 {
    font-weight: 600;
    border-bottom: 1px solid #919195;
    font-size: 15px;
    line-height: 24px;
    padding: 16px 32px;
    margin: 0;
    color: #383a3c;
}

.mp-overlap .mp-level.mp-level-overlay > h2::before {
	opacity: 1;
	transition: transform 0.3s, opacity 0.3s;
	transform: translateX(0);
}

.mp-menu ul li > a {
	display: block;
	outline: none;
    color: black;
    padding: 12px 8px 12px 36px;
    display: block;
    font-size: 16px;
    font-weight: normal;
    &:hover {
        text-decoration: none;
    }
}

.mp-menu ul li::before {
	position: absolute;
	left: 10px;
	z-index: -1;
	color: rgba(0,0,0,0.2);
	line-height: 3.5;
}

.mp-level > ul > li:first-child > a {
	/*box-shadow: inset 0 -1px rgba(0,0,0,0.2), inset 0 1px rgba(0,0,0,0.2);*/
}

.mp-menu ul li a:hover,
.mp-level > ul > li:first-child > a:hover {
    text-decoration: none;
}

.mp-menu .mp-level.mp-level-overlay > ul > li > a,
.mp-level.mp-level-overlay > ul > li:first-child > a {
	box-shadow: inset 0 -1px rgba(0,0,0,0);
}

.mp-level > ul > li:first-child > a:hover,
.mp-level.mp-level-overlay > ul > li:first-child > a {
	box-shadow: inset 0 -1px rgba(0,0,0,0), inset 0 1px rgba(0,0,0,0);
} /* seems like Chrome 34.0.1847.131 needs the second shadow otherwise the transition breaks */

.mp-back {
    background-color: #c9cbcc;
    display: block;
    color: #383a3c;
    font-weight: 600;
    padding: 8px 0 11px 21px;
    font-size: 13px;
    > span {
        font-size: 20px;
    }
}

.mp-menu .mp-level.mp-level-overlay > .mp-back,
.mp-menu .mp-level.mp-level-overlay > .mp-back::after {
	background: transparent;
	box-shadow: none;
	color: transparent;
}

/* Fallback example for browsers that don't support 3D transforms (and no JS fallback) */
/* We'll show the first level only */
/*.no-csstransforms3d .mp-pusher,
.no-js .mp-pusher {
	padding-left: 300px;
}*/

.no-csstransforms3d .mp-menu .mp-level,
.no-js .mp-menu .mp-level {
	display: none;
}

.no-csstransforms3d .mp-menu > .mp-level,
.no-js .mp-menu > .mp-level {
	display: block;
}

.mp-level li.icon-arrow-right {
    background: url(/custom/build/images/nav-item-arrow.png) 100% center no-repeat;
    padding-right: 10%;
}
.mp-menu .nav-heading {
    color: #383a3c;
    padding: 16px 8px 16px 51px;
    border-top: 1px solid #dededc;
    display: block;
    font-size: 16px;
    font-weight: 400;
    font-family: 'Open Sans',sans-serif;
}

.mp-menu .mobile-tertiary {
    margin-top: 30px;
}
