.sf_cols.bullet-lists {
  @media(min-width: 991px) {
    margin: 0 auto;
    width: 64% !important;
  }
}

.bullet-lists__list {
  @media(max-width: 767px) {
    margin-top: 0 !important;
  }

  @media(max-width: 991px) {
    margin-top: 0 !important;
  }

  ul {
    width: 90%;
    margin: 0 auto;

    @media(min-width: 767px) {
      width: 100%;
      margin: 0;
    }
  }
}

.lists__icon-list {
  p {
    margin-bottom: 0;
  }
}


.lists__levy-benefits {
  @include rem((
    margin-top: 16px
  ));

}

.list__check-list {

  .sfContentBlock {
      text-align: center;
      margin-bottom: 15px;

      &:before {
          width: 35px;
          height: 30px;
          background: url(/images/default-source/responsive-theme/green-tick.png?sfvrsn=2) center 0 no-repeat;
          display: block;
          text-align: center;
          margin: 0 auto 15px;
          content: "";
      }
  }

  .centred-row {

    padding-top: 0;

    .container {
      @media(min-width: 992px) {
        width: 720px;
      }
    }
  }
}


.link-group {
    text-align: left;
    a {
        text-align: left;
        font-weight: 600;
        margin-bottom: 1.25rem;
        display: block;

        @include rem((
          font-size: 18px
        ));

    }
    p {
        margin: 0;
        text-align: left;

        @include rem((
          margin-bottom: 25px
        ));

    }
}

.list-block {
    max-width: 940px;
    margin: 0 auto;
    h3 {
        color: #383a3c;
        font-size: 18px;
        margin-bottom: 20px;
        line-height: 27px;
        font-weight: 600;
    }
    p {
        margin-bottom: 48px;
    }
}


.study-method-list {

  img {
    width: 100%;
  }

  .study-method-quote {
    @include rem((
      padding: 20px,
      height: 167px,
      font-size: 18px,
      margin-top: -1px
    ));

    @media (min-width: $screen-md-min) {
      @include rem((
        height: 266px
      ));
    }

    @media(min-width: $screen-lg-min) {
      @include rem((
        height: 216px
      ))
    }

    font-style: italic;

    p {
      margin: 0;
      text-align: center;
      color: #383a3c;
    }

    &--blue {
      background: #7bcce5;
    }

    &--pink {
      background: #f0a3c7;
    }

    &--yellow {
      background: #ffc425;
    }

    &--green {
      background: #c1d82f;
    }

  }
}

.study-method-list__footer {

  @include clearfix-micro();

  background: #fff;
  text-align: center;

  @include rem((
    padding: 20px,
    height: 190px,
    margin-bottom: 20px
  ));

  @media(min-width: $screen-md-min) {
    margin-bottom: 0;

    @include rem((
      height: 222px
    ));

  }

  h3 {
    text-transform: uppercase;
    font-weight: normal;

    @include rem((
      font-size: 16px,
    ));

    span,
    a{
      text-transform: none;
      font-weight: 400;
      display: block;

      @include rem((
        font-size: 21px,
        margin-top: 5px
      ));
    }
  }
}
